// pages/post-job.js
import { useState } from "react";
import toast from "react-hot-toast";

const ON_SITE = [
  "Plumbing", "Electricity", "Painting", "Carpentry", "A/C Maintenance & Cleaning",
  "Satellite & TV Installation", "Internet & Wi-Fi Setup", "Home Deep Cleaning",
  "Office Cleaning", "Car Wash (Mobile)", "Private Tutor (Home)", "Babysitting & Child Care",
  "Pet Care & Dog Walking", "Furniture Assembly", "Curtain & Blinds Installation",
  "Wallpaper & Vinyl Wrap", "Plaster & Gypsum Board", "Tiling & Marble Works",
  "Flooring (Parquet, Laminate, Vinyl)", "Swimming-Pool Cleaning", "Gardening & Landscaping",
  "Glass & Aluminium Works", "CCTV & Security Cameras", "Locksmith & Key Duplication",
  "Appliance Repair (Fridge, Washer, etc.)", "Water-Heater Repair", "Carpet & Sofa Cleaning",
  "Pest Control (Cockroaches, Rats, Termites)", "Moving & Transport", "Handyman (General Fixes)",
  "Generator Maintenance", "Solar-Water-Heater Service", "Roofing & Leak Fix", "Junk Removal & Disposal",
];

const ONLINE = [
  "Graphic-Design", "Logo-Design", "Web-Development", "App-Development",
  "E-Commerce Store Setup", "Shopify-Build", "SEO Services", "Social-Media Management",
  "Content-Creation", "Copywriting", "Translation", "Video-Editing", "Virtual-Assistant",
  "Data-Entry", "Online-Tutoring (All Subjects)", "CV/Resume Writing",
  "Career-Coaching (Zoom)", "Financial-Planning (Zoom)",
];

const DISTRICTS = [
  "Achrafieh", "Aley", "Baabda", "Baalbek", "Batroun", "Bsharri", "Chouf",
  "Dahieh", "Jbeil", "Jezzine", "Keserwan", "Matn", "Nabatieh", "Saida",
  "Sour", "Tripoli", "Zahle", "Zgharta-Ehden",
];

export default function PostJob() {
  const [branch, setBranch] = useState("");
  const [service, setService] = useState("");
  const [district, setDistrict] = useState("");
  const [desc, setDesc] = useState("");
  const [budget, setBudget] = useState(10);
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);

    const payload = { branch, service, district, description: desc, budget };

    try {
      const res = await fetch("/api/jobs", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });

      if (res.ok) {
        toast.success("✅ تم النشر بنجاح! ستصلك العروض قريباً.", { duration: 4000 });
        setBranch(""); setService(""); setDistrict(""); setDesc(""); setBudget(10);
      } else {
        toast.error("حدث خطأ أثناء النشر. حاول مجدداً.");
      }
    } catch (err) {
      toast.error("تعذر الاتصال بالخادم 😕");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-[80vh] px-4 bg-orange-50">
      <div className="bg-white/90 backdrop-blur-sm shadow-lg rounded-2xl p-8 w-full max-w-md border border-orange-100">
        <h1 className="text-2xl font-bold text-center text-orange-600 mb-6">
          انشر مهمة جديدة
        </h1>

        <form onSubmit={handleSubmit} className="space-y-5 text-right">
          {/* Branch selector */}
          <div>
            <label className="block text-gray-700 mb-1">نوع الخدمة</label>
            <select
              value={branch}
              onChange={(e) => { setBranch(e.target.value); setService(""); }}
              required
              className="w-full border rounded-lg px-3 py-2 focus:ring-2 focus:ring-orange-400"
            >
              <option value="">-- اختر الفرع --</option>
              <option value="on-site">خدمات في الموقع</option>
              <option value="online">خدمات أونلاين</option>
            </select>
          </div>

          {/* Dynamic category list */}
          {branch && (
            <div>
              <label className="block text-gray-700 mb-1">الخدمة الفرعية</label>
              <select
                value={service}
                onChange={(e) => setService(e.target.value)}
                required
                className="w-full border rounded-lg px-3 py-2 focus:ring-2 focus:ring-orange-400"
              >
                <option value="">-- اختر الخدمة --</option>
                {(branch === "on-site" ? ON_SITE : ONLINE).map((c) => (
                  <option key={c} value={c}>{c}</option>
                ))}
              </select>
            </div>
          )}

          {/* District */}
          <div>
            <label className="block text-gray-700 mb-1">القضاء</label>
            <select
              value={district}
              onChange={(e) => setDistrict(e.target.value)}
              required
              className="w-full border rounded-lg px-3 py-2 focus:ring-2 focus:ring-orange-400"
            >
              <option value="">-- اختر القضاء --</option>
              {DISTRICTS.map((d) => (
                <option key={d} value={d}>{d}</option>
              ))}
            </select>
          </div>

          {/* Description */}
          <div>
            <label className="block text-gray-700 mb-1">وصف المهمة</label>
            <textarea
              value={desc}
              onChange={(e) => setDesc(e.target.value)}
              required
              rows="4"
              placeholder="اشرح ما تحتاجه بالتفصيل"
              className="w-full border rounded-lg px-3 py-2 focus:ring-2 focus:ring-orange-400"
            />
          </div>

          {/* Budget */}
          <div>
            <label className="block text-gray-700 mb-1">الميزانية (USD)</label>
            <input
              type="number"
              value={budget}
              onChange={(e) => setBudget(Number(e.target.value))}
              min="10"
              required
              className="w-full border rounded-lg px-3 py-2 focus:ring-2 focus:ring-orange-400"
            />
          </div>

          {/* Submit */}
          <button
            type="submit"
            disabled={loading}
            className={`w-full py-3 text-white rounded-lg font-semibold transition-all duration-300 ${
              loading
                ? "bg-orange-300 cursor-not-allowed"
                : "bg-orange-500 hover:bg-orange-600 shadow-md"
            }`}
          >
            {loading ? "جاري النشر..." : "أنشر المهمة"}
          </button>
        </form>
      </div>
    </div>
  );
}
